
import axios from 'axios'
import Divider from 'vux/src/components/divider/index.vue'
import Card from 'vux/src/components/card/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'


export default {
directives: {
TransferDom
},
name: "AllBook",
components: {
Divider,
Card,
Popup,
},
data () {
return{
base_url: 'static/books/',
showDirWin: false,
book_list: [],
book_id: '',
chapters: 0,
}
},
mounted(){
this.refresh()
},
methods:{
refresh(){
axios.get('static/tsconfig.json').then((result) => {
this.book_list = result.data.book_list;
});
},
show_log(){
console.log(this.book_list)
},
show_directory(book_id, chapters){
this.book_id = book_id;
this.chapters = chapters;
this.showDirWin = true
},
showDetail(dir_no){
const url = this.base_url + this.book_id + '/' + dir_no.toString() + '.html';
window.open(url)
}
}
}



