
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import MyBook from "./MyBook";
import AllBook from "./AllBook";

export default {
components: {
Tabbar,
TabbarItem,
'AllBook': AllBook,
'MyBook': MyBook,
},
data () {
return {
scrollAddress: '',
}
},
methods: {
showWin(val){

if (val==='AllBook'){
document.getElementById('MyBook').classList.add('no-show');
document.getElementById('AllBook').classList.remove('no-show');
document.getElementsByTagName("html")[0].scrollTop = this.scrollAddress
}
else {
this.scrollAddress = document.getElementsByTagName("html")[0].scrollTop;
document.getElementById('AllBook').classList.add('no-show');
document.getElementById('MyBook').classList.remove('no-show')
}
}
}
}



